
	import {defineComponent,ref,toRefs,reactive,onMounted,watch,computed} from 'vue';
	import {useStore} from 'vuex'
	import {useRoute,useRouter} from 'vue-router'
	export default defineComponent({
		name: 'Cart',
		components: {},
		setup(props, context) {
			onMounted(()=>{
				console.log(useStore());
			});
			const store = useStore();
			const router = useRouter();
			let checklist = ref([]);
			let checkall = ref(false);
			let cartLists = store.getters.getCartLists;
			watch(checkall,function(val:boolean){
				if(val){
					checklist.value.splice(0);
					cartLists.forEach(function(val:any,i:never){
						checklist.value.push(i);
					});
				}else{
					checklist.value.splice(0);
				}
			});
			const goback = ()=>{
				router.back();
			}
			function cart(){
				function addNum(i:number){
					store.dispatch('cmAddNum', {index: i});
				}
				function reduceNum(i:number){
					store.dispatch('cmReduceNum', {index: i});
				}
				function delGood(i:number){
					store.dispatch('cmDelGood',{index:i});
				}
				let total = computed(()=>{
					let sum = 0;
					checklist.value.forEach(function(val:number){
						sum +=cartLists[val].price*cartLists[val].nums;
					});
					return sum;
				});
				
				return {addNum,reduceNum,delGood,total,checklist,checkall,cartLists};
			}
			return {...cart(),goback};
		}
	});
	
